.about-container {
    background-color: #0d0d0d;
    color: #fff;
    padding: 2rem;
    font-family: 'Arial', sans-serif;
  }
  
  .section-title {
    font-size: 2rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid #ffd700;
    display: inline-block;
  }
  
  .summary-section, .skills-section, .experience-section, .education-section, .achievements-section {
    margin-bottom: 2rem;
    padding: 1.5rem;
    background-color: #1a1a1a;
    border-radius: 10px;
  }
  
  .summary-text {
    font-size: 1.2rem;
    line-height: 1.6;
  }
  
  .skills-list {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  
  .experience, .education {
    margin-bottom: 1.5rem;
  }
  
  .experience h3, .education h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .experience ul, .achievements-list {
    list-style-type: square;
    margin-left: 1.5rem;
  }
  
  .achievements-list li {
    margin-bottom: 0.5rem;
  }

  .skills-section h2 {
    color: #fff;
    border-bottom: 2px solid #ffd700;
    display: inline-block;
    margin-bottom: 20px;
    font-size: 2rem;
  }
  
  .skills-icons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .skill-item {
    text-align: center;
    color: #fff;
    font-size: 1.2rem;
    width: 100px;
    transition: transform 0.3s, filter 0.3s;
  }
  
  .skill-item i {
    font-size: 3rem;
    margin-bottom: 10px;
    transition: transform 0.3s, filter 0.3s;
  }
  
  .skill-item:hover i {
    transform: scale(1.2) rotate(10deg);
    filter: grayscale(100%);
  }
  
  .skill-item p {
    margin-top: 5px;
    font-size: 1rem;
  }
  
  .font-link {
    font-family: 'Hanalei Fill', cursive;
  }