.contact-container {
    background-color: #0d0d0d;
    color: #fff;
    padding: 2rem;
    font-family: 'Arial', sans-serif;
    text-align: center;
  }
  
  .section-title {
    font-size: 2rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid #ffd700;
    display: inline-block;
  }
  
  .contact-description {
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }
  
  .contact-form {
    max-width: 600px;
    margin: 0 auto;
    background-color: #1a1a1a;
    padding: 2rem;
    border-radius: 10px;
  }
  
  .form-group {
    margin-bottom: 1.5rem;
    text-align: left;
  }
  
  .form-group label {
    display: block;
    font-size: 1rem;
    margin-bottom: 0.5rem;
    color: #ffd700;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    border: 1px solid #ffd700;
    border-radius: 5px;
    background-color: #2a2a2a;
    color: #fff;
  }
  
  .form-group input::placeholder,
  .form-group textarea::placeholder {
    color: #aaa;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #e6b800;
  }
  
  .submit-button {
    padding: 0.8rem 2rem;
    font-size: 1rem;
    background-color: #ffd700;
    color: #0d0d0d;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .submit-button:hover {
    background-color: #e6b800;
  }
  
  .success-message {
    margin-top: 1.5rem;
    font-size: 1rem;
    color: #00ff00;
  }

  .font-link {
    font-family: 'Hanalei Fill', cursive;
  }